@import "src/shared/consts";

.record {
  @include clear();
  font-family: inherit;
  color: #D0021B;
  display: flex;
  align-items: center;
  cursor: pointer;
  
  &-button {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    border: 2px solid;
    display: block;
    margin-right: 10px;
    position: relative;
    
    &:before {
      content: '';
      border-radius: 50px;
      left: 50%;
      top: 50%;
      width: 32px;
      height: 32px;
      transform: translate(-50%, -50%);
      background: #D0021B;
      position: absolute;
    }
    
    &.-stop {
      background: #D0021B;
    }
    
    &.-stop:before {
      border-radius: 2px;
      width: 20px;
      height: 20px;
      background: #FFFFFF;
    }
  }
  
  &-time {
    color: #333;
  }

  &-time > strong {
    font-weight: $bold;
    color: #D0021B; 
  }
}