@import "src/shared/consts";

.workplaces {
  &-tree {
    padding: 0;
    margin: -10px -10px 0;
    list-style: none;
    
    ul {
      margin: 0 0 5px 0;
      padding: 0;
    }
  }

  &-link {
    @include clear();
    padding: 7px 10px 7px 30px;
    border-radius: $br;
    width: 100%;
    text-align: left;

    &:hover {
      background-color: $color-menu-hover;
    }

    &:active {
      background-color: $color-menu-active;
    }
  }
  
  &-tree ul > li > &-link {
    padding-left: 50px;
  }

  &-tree ul ul > li > &-link {
    padding-left: 70px;
  }
}