@import "src/shared/consts";

.button {
  @include clear();
  display: inline-block;
  font-size: 13px;
  line-height: 19px;
  padding: 6px 20px 7px 20px;
  border-radius: $br;
  background: $color-primary;
  color: #FFFFFF;
  cursor: pointer;
  box-shadow: inset 0 0 0 1px darken($color-primary, 2%), inset 0 2px 0 rgba(255,255,255,0.1);
  background-image: linear-gradient(to bottom, rgba(white, 0.1), rgba(white, 0));
  text-decoration: none;
  
  > span {
    display: block;
  }
  
  &.-small {
    padding: 3px 10px 4px 10px;
    border-radius: 20px;
  }
  
  &:hover {
    background-image: none;
    box-shadow: inset 0 0 0 1px darken($color-primary, 2%);
  }
  
  &:active {
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.1)
  }
  
  &:active > span {
    transform: translate(0, 1px);
  }

  &.-light {
    background: change-color($color-primary, $lightness: 97%, $saturation: 50%);
    color: darken($color-primary, 5%);
    box-shadow: inset 0 0 0 1px change-color($color-primary, $lightness: 90%, $saturation: 50%);

    &:hover {
      background: change-color($color-primary, $lightness: 96%);
      box-shadow: inset 0 0 0 1px change-color($color-primary, $lightness: 87%);
    }
  }
}