@import 'src/shared/consts';

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(white, 0.6);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  
  &-window {
    background: white;
    border-radius: 3px;
    box-shadow: 0 2px 10px -3px rgba(0,0,0,0.2);
    padding: 30px 20px;
    min-width: 400px;
  }
  
  &-buttons {
    margin: 20px 0 -10px 0;
    display: flex;
    justify-content: flex-end;
    
    > * + * {
      margin-left: 10px;
    }
  }
}