$color-primary: #00A0E4;
$color-header: change-color($color-primary, $lightness: 30%, $saturation: 80%);
$color-separator: change-color($color-primary, $lightness: 90%, $saturation: 5%);
$color-menu-hover: change-color($color-primary, $lightness: 98%, $saturation: 50%);
$color-menu-active: change-color($color-primary, $lightness: 96%, $saturation: 50%);
$br: 3px;

$bold: 700;
$regular: 400;

@mixin clear() {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  outline: none;
  padding: 0;
  margin: 0;
}